.navbar{
    z-index: 99;
    position: sticky;
    top: 0px;
    padding: 15px;
    z-index: 99;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}
.icon{
    
    height: 50px;
    width: 50px;
}
.navItems{
    min-width: 450px;
    display: flex;
    justify-content: space-around;
    color: rgb(51, 51, 51);;
   
}

.navItems li{
    font-weight: 700;
    list-style: none;
}

.navItems a:hover, a:visited, a:link, a:active {
    text-decoration: none;
}

.navItems a:visited{
    color: rgb(51, 51, 51);
}

.email{
  display: none;
  color: #373737;
}

.icon:hover h4{
  display: flex;
}
